.container{
  max-width: 280px;
  color: #e5e5e5;
  border-style: solid;
  border-width: thin;
  box-shadow: 0 1px 3px 1px #d3740042;
  border-color: #343a40;
  border-radius: 20px;
  padding-top: 10px;
  padding-bottom: 10px;
}
.customize-short{
  color: var(--color-text);
  width:95px;
}
.customize-long{
  color: var(--color-text);
  width:190px;
}

.badge{
 font-size: 0.85em;
  font-weight: 500;
}

@media screen and (min-width: 487px) {
  span:after {
    content: attr(data-suffix);
  }
}
